/*
|--------------------------------------------------------------------------
| Variables
|--------------------------------------------------------------------------
|
*/

$bg: #f7f7f7;

/*
|--------------------------------------------------------------------------
| Main
|--------------------------------------------------------------------------
|
*/

body {
    background-color: $bg;
}

/*
|--------------------------------------------------------------------------
| Login
|--------------------------------------------------------------------------
|
*/

body.login {

    .master {
        max-width: 600px;
        margin-top: 30px;
    }
}

/*
|--------------------------------------------------------------------------
| Alerts system
|--------------------------------------------------------------------------
|
*/

#main-alert {
    position: relative;
    z-index: 900;
    margin-bottom: 0;
    padding-top: 15px;
    padding-right: 0;
    padding-bottom: 7px;
    padding-left: 0;
    border-top: none;
    border-right: 0;
    border-left: 0;
    border-radius: 0;

    .container {
        position: relative;

        button {
            position: absolute;
            top: 0;
            right: 25px;
        }
    }

    p {
        padding-right: 35px;
    }
}

/*
|--------------------------------------------------------------------------
| Nav bar
|--------------------------------------------------------------------------
|
*/

.navbar {
    margin-bottom: 5px;
    background-color: $bg;
    border-top: none;
    border-right: 0;
    border-left: 0;
    border-radius: 0;
    box-shadow: 0 0 0 2px rgba(0,0,0,0.03);

    .form-logout {
        margin: 0;

        button {
            appearance: none;
            -moz-appearance: none;
            -webkit-appearance: none;
            margin: 0;
            padding: 15px;
            background: none;
            border: 0;
            color: #777;
            line-height: 20px;

            &:hover {
                color: #333;
                background-color: transparent;
            }
        }
    }
}

/*
|--------------------------------------------------------------------------
| Breadcrumbs
|--------------------------------------------------------------------------
|
*/

.breadcrumb {
    margin-bottom: 10px;
    padding-right: 0;
    padding-left: 0;
    background-color: $bg;
}

/*
|--------------------------------------------------------------------------
| Page headings
|--------------------------------------------------------------------------
|
*/

.master {

    & > .page-header {
        margin-top: 15px;
    }
}

.main-content {

    & > .page-header {
        margin-top: 5px;
    }
}

.page-header {
    position: relative;
    margin-top: 30px;
    line-height: 42px;

    &.section-header {
        margin-top: 0;
    }
}

.after-title {
    margin-top: 10px;
    text-align: left;

    .btn {
        position: relative;
        top: -2px;
    }
}

/*
|--------------------------------------------------------------------------
| Tabs
|--------------------------------------------------------------------------
|
*/

.tab-pane {
    display: none;

    &.active {
        display: block;
    }
}

.nav-tabs {
    margin-bottom: 30px;

    & > li {

        &.active > a,
        &.active > a:hover,
        &.active > a:focus {
            background-color: $bg;
        }
    }
}

/*
|--------------------------------------------------------------------------
| Items table
|--------------------------------------------------------------------------
|
*/

table {

    tr {

        th,
        td {
            min-width: 90px;
        }

        th {
            a {
                color: #333;

                &:hover,
                &:active {
                    color: #111;
                }

                &.active {
                    color: #428bc8;

                    & + .caret {
                        border-top-color: #428bc8;
                    }

                    &:hover,
                    &:active {
                        color: #2a6496;

                        & + .caret {
                            border-top-color: #2a6496;
                        }
                    }

                    &.desc {

                        & + .caret {
                            border-top: none;
                            border-bottom: 4px solid #428bc8;
                        }

                        &:hover,
                        &:active {

                            & + .caret {
                                border-bottom-color: #2a6496;
                            }
                        }
                    }
                }

                & + .caret {
                    margin-left: 6px;
                }
            }
        }

        td {
            padding-left: 0;
            padding-right: 0;

            a {
                display: block;
                width: 100%;
                height: 100%;
                min-height: 1em;
                color: #666;

                &:hover,
                &:focus,
                &:active {
                    color: #666;
                    text-decoration: none;
                }
            }
        }
    }

    .cell-editable-inline {
        cursor: pointer;

        &.cell-bool,
        &.cell-boolean {
            text-align: center;
        }
    }
}

.pagination {
    margin-top: 0;
}

/*
|--------------------------------------------------------------------------
| Items gallery
|--------------------------------------------------------------------------
|
*/

.gallery-item{
    overflow: hidden;
}

/*
|--------------------------------------------------------------------------
| Tooltips
|--------------------------------------------------------------------------
|
*/

.tooltip {
    white-space: nowrap;
}

/*
|--------------------------------------------------------------------------
| Buttons
|--------------------------------------------------------------------------
|
*/

.btn-default {
    background-color: $bg;
}

.btn {
    transition: 125ms ease-in-out;

    &:focus {
        outline: none;
    }

    @keyframes loading {
        to {
            transform: rotate(360deg);
        }
    }

    &.loading {
        position: relative;
        padding-right: 2em;
        min-width: 24px;
        min-height: 24px;

        &:after {
            content: '';
            position: absolute;
            top: 0.7em;
            right: 0.35em;
            width: 1em;
            height: 1em;
            border-radius: 50%;
            border: 3px dotted rgba(255,255,255,0.6);
            border-top-color: rgba(255,255,255,1);
            animation: loading .9s linear infinite;
        }
    }

    &.btn-default {

        &.loading {

            &:after {
                border-color: darken($bg, 30%);
                border-top-color: darken($bg, 60%);
            }
        }
    }
}

/*
|--------------------------------------------------------------------------
| Form & inputs
|--------------------------------------------------------------------------
|
*/

form {
    margin-bottom: 1em;
}

#main-form {
    padding-bottom: 3px;
}

.bottom-buttons {
    overflow: hidden;
    margin-top: 30px;
    padding-top: 20px;
    border-top: 1px solid #eee;

    button {
        margin-left: 15px;

        &:first-child {
            margin-left: 0;
        }

        &.glyphicon {
            top: 0;
        }
    }

    .delete {
        color: #666;
    }
}

.form-control:focus {
    border-color: #66afe9;
    box-shadow: 0 0 7px rgba(39,128,248,.15);
    outline-offset: 0;
    outline: 0;
}

.form-control-feedback {
    right: 8px;
}

.input-group + .form-control-feedback {
    display: none;
}

.editable-html {
    font-size: 0.75em;
    font-family: Monaco, monospace;
    white-space: pre;
}

.password-toggle-label {
    margin-bottom: 0;
    font-weight: normal;
    margin-top: 2px;
    cursor: pointer;

    input {
        margin-right: 5px;
        position: relative;
        top: -1px;
    }
}

/*
|--------------------------------------------------------------------------
| TinyMCE
|--------------------------------------------------------------------------
|
*/

.form-group {

    .mce-tinymce {
        border-radius: 4px;
    }

    .mce-panel {
        border-color: #ccc;
    }

    .mce-toolbar-grp {
        background: #f0f0f0;
        border-top-left-radius: 4px;
        border-top-right-radius: 4px;
    }

    .mce-btn {
        background: none;

        &:hover {
            background: darken(#f0f0f0, 10%);
        }
    }

    .mce-edit-area,
    .mce-edit-area iframe {
        border-bottom-left-radius: 4px;
        border-bottom-right-radius: 4px;
    }
}

/*
|--------------------------------------------------------------------------
| Chosen
|--------------------------------------------------------------------------
|
*/

.form-group.select {

    .chosen-container-single {
        height: 30px;
        font-size: 14px;

        & > a {
            height: 30px;
            line-height: 28px;

            // Caret
            b {
                position: relative;
                top: 3px;
            }

            // Reset
            .search-choice-close {
                top: 9px;
            }
        }
    }

    .chosen-container-multi {

        .chosen-choices {
            padding: 3px;
            border: 1px solid #ccc;
            border-radius: 4px;

            .search-choice {
                margin-top: 3px;
                margin-bottom: 2px;
            }
        }

        .search-field {
            height: 25px;
            line-height: 27px;

            input[type=text] {
                height: 25px;
            }
        }

        &.chosen-with-drop {

            .chosen-choices {
                border-bottom-right-radius: 0;
                border-bottom-left-radius: 0;
            }
        }

        .chosen-drop {
            border-bottom-right-radius: 4px;
            border-bottom-left-radius: 4px;
        }
    }
}

/*
|--------------------------------------------------------------------------
| Date and time pickers
|--------------------------------------------------------------------------
|
*/

.ui-datepicker {
    z-index: 100!important;

    table tr th,
    table tr td {
        min-width: 0;
    }
}

.form-control[readonly].datepicker,
.form-control[readonly].datetimepicker,
.form-control[readonly].timepicker {
    cursor: pointer;
    background-color: #fff;
}

/*
|--------------------------------------------------------------------------
| Color picker
|--------------------------------------------------------------------------
|
*/

input+.iris-picker{
    position: absolute;
    z-index: 9999;
}

/*
|--------------------------------------------------------------------------
| YouTube
|--------------------------------------------------------------------------
|
*/

.embed-video-wrapper{

    .has-error{
        border-color: #a94442;
        box-shadow: none;
    }

    .preview-box{
        min-height: 200px;
        position: relative;
        text-align: center;
        color: #ccc;

        .placeholders {

            .glyphicon {
                top: 74px;
                font-size: 48px;
            }

            .error {
                display: none;
                color: #ff4500;
            }
        }

        iframe{
            width: 100%;
            min-height: 295px;
        }
    }
}

/*
|--------------------------------------------------------------------------
| Map input
|--------------------------------------------------------------------------
|
*/

.map {
    min-height: 375px;
}

/*
|--------------------------------------------------------------------------
| Responsive styles
|--------------------------------------------------------------------------
|
*/

@media (min-width: 768px) {

    .after-title {
        margin-top: 0;
        text-align: right;
    }
}

/*
|--------------------------------------------------------------------------
| Title buttons
|--------------------------------------------------------------------------
|
*/

.title-btn {
    top: 6px;
    height: 32px;
    width: 32px;
    overflow: hidden;
    vertical-align: top;
    border: 2px solid #bbb;
    -webkit-border-radius: 20px;
    border-radius: 20px;
    cursor: pointer;
    color: #bbb;
    font-size: 15px;
    line-height: 28px;
    text-align: center;

    &:hover,
    &:focus {
        text-decoration: none;
        color: #999;
        border-color: #999;
    }

    &.active {
        color: #fff;
        border-color: #337ab7;
        background-color: #337ab7;
    }
}

/*
|--------------------------------------------------------------------------
| Filter Box
|--------------------------------------------------------------------------
|
*/

.filter-panel {

    &,
    .panel-heading,
    .panel-footer {
        background-color: $bg;
    }

    .table {

        tr {

            th {
                width: 5%;
                text-align: right;
                vertical-align: middle;
            }
        }
    }

    .form-group {
        margin: 5px 0;

        .chosen-choices {
            min-height: 35px;
            background-image: none;
            box-shadow: none;
        }
    }

    form {
        margin-bottom: 0;
    }
}

/*
|--------------------------------------------------------------------------
| Reorder Box
|--------------------------------------------------------------------------
|
*/

.reorder-table {
    width: 100%;
}

.reorder-number {
    min-width: 30px;
    text-align: right;
    padding: 8px;
    margin-bottom: 5px;
}

.reorder-item {
    width: 97%;
    padding: 7px;
    border: 1px solid #ccc;
    margin-bottom: 5px;
    background-color: #fff;

    i {
        display: table-cell;
        vertical-align: middle;
        padding: 0 10px 0 5px;
        color: #a0a0a0;
    }

    span {
          display: table-cell;
    }
}

.grabbable {
    cursor: move; /* fallback if grab cursor is unsupported */
    cursor: grab;
    cursor: -moz-grab;
    cursor: -webkit-grab;

    &:active {
        cursor: grabbing;
        cursor: -moz-grabbing;
        cursor: -webkit-grabbing;
    }
}

.ui-sortable-helper {
    display: table;
}

.sortable-placeholder {
    width: 0px;
    padding: 7px;
    display: block;
}
